@font-face {
  font-family: "seo";
  src: url("../fonts/fonts/seo.eot");
  src: url("../fonts/fonts/seo.eot") format("embedded-opentype"), url("../fonts/fonts/seo.woff") format("woff"), url("../fonts/fonts/seo.ttf") format("truetype"), url("../fonts/fonts/seo.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-seo]:before {
  font-family: "seo" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="seo-"]:before,
[class*=" seo-"]:before {
  font-family: "seo" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.seo-rocket:before {
  content: "\61";
}

.seo-facebook-square:before {
  content: "\62";
}

.seo-twitter-square:before {
  content: "\63";
}

.seo-cog:before {
  content: "\64";
}

.seo-pencil-square-o:before {
  content: "\65";
}

.seo-trash:before {
  content: "\66";
}

.seo-editor {
  display: flex;
  justify-content: space-between;
}

.seo-editor nav {
  width: 40px;
  padding-top: 10px;
  position: relative;
  z-index: 2;
}

.seo-editor nav ul {
  list-style: none;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.seo-editor nav li {
  margin-bottom: 4px;
}

.seo-editor nav a {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: white;
  border: 1px solid #aaaaaa;
  border-radius: 4px 0 0 4px;
  background: #f0f2f4;
  font-size: 16px;
  cursor: pointer;
}

.seo-editor nav a:hover {
  text-decoration: none;
}

.seo-editor nav a.active {
  background: white;
  border-right: 1px solid transparent;
}

.seo-editor .seo-tab-container {
  flex-grow: 1;
  border: 1px solid #aaaaaa;
  background: white;
  border-radius: 4px;
  padding: 20px;
  margin-left: -1px;
  position: relative;
  z-index: 1;
}

.seo-editor .seo-tab {
  display: none;
}

.seo-editor .seo-tab.active {
  display: block;
}

.seo-editor .seo-tab__title {
  border-bottom: 1px solid #e5e8eb;
  padding-bottom: 16px;
}

.seo-editor-container {
  padding-bottom: 1.2308rem;
  margin-bottom: 1rem;
}

.seo-editor-container:after {
  border-bottom: 1px solid #e5e8eb;
  visibility: visible;
  width: 100%;
  content: " ";
  display: block;
  margin-top: 1.2308rem;
}

.seo-editor .seo-vars {
  padding: 0;
  margin: 0 0 20px;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}

.seo-editor .seo-vars li {
  margin: 0 5px 5px 0;
}

.seo-editor .seo-vars .btn {
  padding: 3px 6px;
  margin: 0;
}

.seo-editor .seo-vars .btn:hover {
  color: white;
}

.seo-editor .seo-input {
  margin-bottom: 20px;
}

.seo-editor .seo-input.radio .radio-options {
  display: flex;
}

.seo-editor .seo-input.radio .radio-options label {
  margin-right: 20px;
}

.seo-editor .seo-input.radio .radio-options input {
  margin-right: 10px;
}

.seo-editor .seo-section .fields {
  flex-grow: 1;
  margin-right: 20px;
  width: 100%;
}

@media (min-width: 1200px) {
  .seo-editor .seo-section {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .seo-editor .seo-section .fields {
    max-width: 500px;
    width: 45%;
  }
}

.seo-editor .seo-messages {
  padding-top: 16px;
}

.seo-editor .preview-holder {
  padding: 20px 0;
  font-family: Arial,Helvetica,sans-serif;
  font-style: normal;
  width: 100%;
}

@media (min-width: 1200px) {
  .seo-editor .preview-holder {
    width: 50%;
  }
}

.seo-editor .preview-holder .preview-card {
  padding: 10px 10px 16px;
  border-bottom: 1px hidden #ffffff;
  border-radius: 2px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
}

.seo-editor .preview-holder .preview-card--image {
  display: block;
  width: 100%;
  padding-top: 65.49296%;
  position: relative;
  background-color: #f1f1f1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 20px;
  font-size: 0;
  line-height: 0;
}

.seo-editor .preview-holder .preview-card--image a {
  padding: 0 3px;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -ms-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  font-size: 10px;
  display: flex;
  align-items: center;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
}

.seo-editor .preview-holder .preview-card--image a:hover {
  color: #000000;
  background: #FFFFFF;
  text-decoration: none;
}

.seo-editor .preview-holder .preview-card--image a i {
  font-size: 18px;
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  background: #FFFFFF;
  border-radius: 3px;
}

.seo-editor .preview-holder .preview-card--image a i:before {
  position: relative;
  top: 2px;
}

.seo-editor .preview-holder .preview-card--image div.img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  overflow: hidden;
}

.seo-editor .preview-holder .preview-card--image div.img img {
  position: absolute;
  min-width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.seo-editor .preview-holder .preview-card--actions {
  position: absolute;
  bottom: 20px;
  right: 10px;
  z-index: 999;
  display: flex;
}

.seo-editor .preview-holder h3 {
  color: #1e0fbe;
  font-size: 16px;
  line-height: 20px;
  margin: 0;
  font-weight: 400;
}

.seo-editor .preview-holder .preview-link {
  color: #006621;
  font-style: normal;
  margin: 0 0 9px;
  font-size: 14px;
  line-height: 20px;
  border-bottom: 1px solid #ebebeb;
  padding-bottom: 9px;
}

.seo-editor .preview-holder .preview-description {
  font-size: 14px;
  line-height: 20px;
}

.seo-editor .preview-holder .preview-card.google {
  width: 100%;
}

.seo-editor .preview-holder .preview-card.facebook {
  max-width: 560px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15) inset, 0 1px 4px rgba(0, 0, 0, 0.1);
  position: relative;
  font-family: SF Optimized, system-ui, -apple-system, BlinkMacSystemFont, '.SFNSText-Regular', sans-serif;
  font-size: 12px;
  line-height: 16px;
}

.seo-editor .preview-holder .preview-card.facebook h3 {
  font-family: Georgia, serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  margin-bottom: 4px;
  max-height: 110px;
  overflow: hidden;
  word-wrap: break-word;
  color: black;
}

.seo-editor .preview-holder .preview-card.facebook .preview-link {
  border: 0;
  font-size: 11px;
  line-height: 11px;
  text-transform: uppercase;
  color: #90949c;
  margin: 0;
  padding: 0;
}

.seo-editor .preview-holder .preview-card.twitter {
  max-width: 560px;
  border-radius: .42857em;
  border-width: 1px;
  border-style: solid;
  border-color: #E1E8ED;
  box-sizing: border-box;
  color: inherit !important;
  overflow: hidden;
  padding: 0 0 20px 0;
}

.seo-editor .preview-holder .preview-card.twitter .preview-card--image {
  padding-top: 50.21834%;
  margin-bottom: 20px;
}

.seo-editor .preview-holder .preview-card.twitter .preview-contents {
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  line-height: 1.3em;
  padding: 0;
}

.seo-editor .preview-holder .preview-card.twitter h3 {
  font-size: 14px;
  color: black;
  font-weight: bold;
  padding: 0 14px;
}

.seo-editor .preview-holder .preview-card.twitter .preview-description {
  margin: 0;
  padding: 0 14px;
}

.seo-editor .preview-holder .preview-card.twitter .preview-link {
  margin: 0;
  padding: 0 14px;
  color: #8899A6;
  border: 0;
}

.cms-container--split-mode .seo-editor .seo-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cms-container--split-mode .seo-editor .seo-section .fields {
  width: 100%;
}

.cms-container--split-mode .seo-editor .seo-section .preview-holder {
  max-width: 100%;
}

.badge.status-seowarning,
.badge.status-seogood,
.badge.status-seoerror {
  position: relative !important;
  padding: 0 !important;
  width: 15px;
  height: 15px;
  border-radius: 50% !important;
  border: 1px solid transparent;
  line-height: 14px !important;
  margin-left: 3px !important;
  top: -2px !important;
}

.badge.status-seowarning {
  background-color: #fefbde;
  border-color: #e9d104;
  color: #43536d;
}

.badge.status-seogood {
  background-color: #eaf6e4;
  border-color: #72c34b;
  color: #43536d;
}

.badge.status-seoerror {
  background-color: #fae8e9;
  border-color: #e68288;
  color: #43536d;
}




